.login-form {
    max-width: 360px;
    margin: 60px auto;
    padding: 20px;
  }
  .login-form label {
    display: block;
    margin: 20px 0;
    width: 95%;
  }
  .login-form span {
    display: block;
    margin-bottom: 6px;
  }
  .login-form input {
    padding: 8px 6px;
    font-size: 1em;
    color: #777;
    width: 100%;
    border: none;
  }